import React from "react";
import "./navbar.css";
import {Link} from "react-router-dom";
import logo_small_icon_only_inverted from "../../logo/logo_white_large.png";



class Navbar extends React.Component{

    state={display:"notShow"};

    render(){


        const toggleDisplay = () =>{

            if(this.state.display ==="show"){
                this.setState({display:"notShow"});
            }else{
                this.setState({display:"show"});

            }
        }


        return(

            <div className = "navContainer">
                <div className = "res">
                    <div className = "logo" ><Link to = "/"> <img src = {logo_small_icon_only_inverted} alt="img"/></Link></div>
                    <div className = "title" ><p id = "ptag">Portfolio v1</p></div>
                    {/* <a href="#" onClick = {toggleDisplay} className = "icon" >&#9776;</a> */}
                    <p onClick = {toggleDisplay} className = "icon" >&#9776;</p>

                </div>
                <ul  id = {this.state.display}>
                    <Link to = "/"> <li><p className="nave-home-btn" onClick = {toggleDisplay} href="#homePageNav">home</p> </li> </Link>
                    <li> <a  onClick = {toggleDisplay} href="#journeyPageNav">journey</a>  </li>
                    <li> <a  onClick = {toggleDisplay} href="#projectsPageNav">projects</a></li>
                    <li> <a  onClick = {toggleDisplay} href="#contactPageNav">contact</a>  </li>

                    {/* <Link to = "/resume">
                    <li>  reasume </li>
                    </Link> */}
                </ul>

        </div>


        );
    }


}
export default Navbar;


