import React from "react";
import "./yearlySkillsComponent.css";

const yearlySkillsComponent = (props) =>{
    return(
        <div className = "skillsContainer">
            <h3 className="alignCenter">{props.year}</h3>
            <div className = "skillsContainer1-1">
                <p className="alignLeft" >skills learned</p>
                <div className = "skillsContainer1-1-1">
                    <p className="contentColor">{props.skills}</p>
                </div>
            </div>
            <div className = "skillsContainer1-2">
                <p className="alignLeft" >{props.internship[0]}</p>
                <div className = "skillsContainer1-2-1">
                    <p className="contentColor">{props.internship[1]}</p>
                </div>
            </div>
        </div>
    )
}

export default yearlySkillsComponent;