import React from 'react';
import './journey.css';
import YearlySkillsComponent from "./yearlySkillsComponent";



function journey() {

  const experiance = [{
    year:"Final Year",
    skills:"HTML5, CSS3, Javascript, React.js, Responsive Web Design",
    internship:["Internship","Web developer intern at Msys Technologies"]
    },
    {
      year:"3rd year",
      skills:"Python, Java",
      internship:["Internship","Intern at Projexino Web Technologies"]
    },
    {
      year:"2nd Year",
      skills:"C++, MySQL,",
    },
    {
      year:"1st Year",
      skills:"C Programming, Microsoft Excel / Word / Powerpoint, Google Docs",
    },
    
  ]


  return (
    <div id = "journeyPageNav"  className = "journeyPageContainer">
        <div className = "journeyPagec1">
        <h1>My Journey</h1>

            <div  className = "aboutC2">
                <YearlySkillsComponent year = {experiance[0].year} skills = {experiance[0].skills} internship = {experiance[0].internship} />
                <YearlySkillsComponent year = {experiance[1].year} skills = {experiance[1].skills} internship = {experiance[1].internship} />
                <YearlySkillsComponent year = {experiance[2].year} skills = {experiance[2].skills} internship = "" />
                <YearlySkillsComponent year = {experiance[3].year} skills = {experiance[3].skills} internship = "" />
            </div>
            
            
        </div>
    </div>
  );
}

export default journey;
